@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,200&family=Open+Sans:wght@300&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin:0;  
  font-family: 'Mulish', sans-serif;
 }
.navbar{
  background-color: #fff !important;
}
.brand-name{
  font-size: 2.0rem;
  font-weight: bold;
  text-transform: capitalize;
  color: #3498db !important ;
}
.navbar-brand{
  font-size: 2.0rem;
  font-weight: bold;
  text-transform: capitalize;
  color: #444077 !important ;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387 !important;
}
.navbar a:hover{

  color: #3498db;
}
@media (max-width :991px) {
  .navbar-brand{
    font-size: 1.5rem !important;

  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
  
  }
#header h1 {
margin: 0 0 10px 0;
font-size: 48px;
font-weight: 700;
color: black !important;
}

#header .brand-name{
  color: #3498db;
}
#header h2 {
  font-size: 24px;
  color: #484848
  }
  #header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px  30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db ;
    text-decoration: none;
  }
  #header .btn-get-started:hover{
    background: #1894e7;
    

  }
  .header-img {
    text-align: right;
  }
  #header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
  }

  @-webkit-keyframes up-down {
    0%{
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
    100%{
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
  }
@keyframes up-down{
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

}



